<template>
    <div id="textmessage">
        <div class="line">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item :to="{ path: '/First' }">首页管理</el-breadcrumb-item>
                <el-breadcrumb-item>考试信息</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        学院名称：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        <el-select v-model="value1" placeholder="请选择" clearable>
            <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
            </el-option>
        </el-select>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        监考教师：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<el-input v-model="input1" placeholder="请输入内容" style="width:221.4px"></el-input>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        考试科目：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<el-input v-model="input2" placeholder="请输入内容" style="width:221.4px"></el-input>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        <el-button type="info" plain>查询</el-button>
        <el-divider></el-divider>
        <el-button type="info" plain icon="el-icon-circle-plus-outline" @click="dialogFormVisible = true">增加</el-button>
            <el-dialog title="编辑" :visible.sync="dialogFormVisible" width="35%">
                <el-form :model="formLabelAlign" label-width="120px" style="width:400px">
                    <el-form-item label="考试类别:" :label-width="formLabelWidth">
                        <el-input v-model="formLabelAlign.tobj" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="监考教师:" :label-width="formLabelWidth">
                        <el-input v-model="formLabelAlign.ltr" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="学院名称:" :label-width="formLabelWidth">
                        <el-select v-model="value2" clearable placeholder="请选择" style="width:280px">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="专业:" :label-width="formLabelWidth">
                        <el-input v-model="formLabelAlign.spe" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="开始结束-年月日:" :label-width="formLabelWidth">
                    <div class="block">
                        <el-date-picker
                        v-model="value3"
                        type="daterange"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期">
                        </el-date-picker>
                    </div>
                    </el-form-item>
                    <el-form-item label="开始结束-时分秒:">
                     <el-time-picker
                        is-range
                        v-model="value4"
                        range-separator="至"
                        start-placeholder="开始时间"
                        end-placeholder="结束时间"
                        placeholder="选择时间范围">
                    </el-time-picker>
                    </el-form-item>
                    <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
                    </div>
                </el-form>
            </el-dialog>
        <el-button type="info" plain icon="el-icon-delete" @click="open">删除</el-button>
        <el-button type="info" plain icon="el-icon-edit" @click="dialogFormVisible = true">更改</el-button>
            <el-dialog title="编辑" :visible.sync="dialogFormVisible" width="35%">
                <el-form :model="formLabelAlign" label-width="120px" style="width:400px">
                    <el-form-item label="考试类别:" :label-width="formLabelWidth">
                        <el-input v-model="formLabelAlign.tobj" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="监考教师:" :label-width="formLabelWidth">
                        <el-input v-model="formLabelAlign.ltr" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="学院名称:" :label-width="formLabelWidth">
                        <el-select v-model="value2" clearable placeholder="请选择" style="width:280px">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="专业:" :label-width="formLabelWidth">
                        <el-input v-model="formLabelAlign.spe" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="开始结束-年月日:" :label-width="formLabelWidth">
                    <div class="block">
                        <el-date-picker
                        v-model="value3"
                        type="daterange"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期">
                        </el-date-picker>
                    </div>
                    </el-form-item>
                    <el-form-item label="开始结束-时分秒:">
                     <el-time-picker
                        is-range
                        v-model="value4"
                        range-separator="至"
                        start-placeholder="开始时间"
                        end-placeholder="结束时间"
                        placeholder="选择时间范围">
                    </el-time-picker>
                    </el-form-item>
                    <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
                    </div>
                </el-form>
            </el-dialog>
        <el-button type="info" plain icon="el-icon-document">导入考场信息</el-button>
        <el-table
    :data="tableData"
    height="435"
    border
    :header-cell-style="{background:'#eef1f6',color:'#606266'}"
    :row-style="{height:'40px'}"
    style="width: 100%">
    <el-table-column
      align="center"
      prop="objname"
      label="考试类别名称"
      width="">
    </el-table-column>
    <el-table-column
      align="center"
      prop="ltr"
      label="监考教师"
      width="">
    </el-table-column>
    <el-table-column
      align="center"
      prop="clo"
      label="院系"
      width="">
    </el-table-column>
      <el-table-column
      align="center"
      prop="spe"
      label="专业"
      width="">
      </el-table-column>
      <el-table-column
      align="center"
      prop="begin"
      label="开始时间"
      width="">
      </el-table-column>
      <el-table-column
      align="center"
      prop="end"
      label="结束时间"
      width="">
      </el-table-column>
  </el-table>
    </div>
</template>


<script>
export default {
    name: 'textmessage',
    data() {
        return{
            input1: '',
            input2: '',
            options: [{
                value: '选项1',
                label: '英语'
            }],
            value1: '',
            value2: '',
            value3: '',
            value4: '',
            dialogFormVisible: false,
            dialogFormVisible2: false,
            formLabelAlign: {
                tobj:'',
                ltr:'',
                spe:''
            },
            tableData: [{
         
            }]
        }
    },
    methods: {
        open() {
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      }
    }
}
</script>

<style scoped>
    .line{
    width: 200px;
    height: 60px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
  }
  .el-table{
  border: 1px solid #eef1f6;
  position: relative;
  top: 20px;
}
</style>


