<template>
  <div class="rmessage">
    <div class="line2">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/First' }">首页管理</el-breadcrumb-item>
        <el-breadcrumb-item>考场信息</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-divider></el-divider>
      <el-button type="info" plain icon="el-icon-circle-plus-outline" @click="dialogFormVisible = true">增加</el-button>
        <el-dialog title="编辑" :visible.sync="dialogFormVisible" width="33%">
          <el-form :model="formLabelAlign" label-width="100px" style="width:400px">
            <el-form-item label="地点:" :label-width="formLabelWidth">
              <el-input v-model="formLabelAlign.location" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="教室号:" :label-width="formLabelWidth">
              <el-input v-model="formLabelAlign.number" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="校区:" :label-width="formLabelWidth">
              <el-input v-model="formLabelAlign.area" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="楼栋:" :label-width="formLabelWidth">
              <el-input v-model="formLabelAlign.building" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="教室容量:" :label-width="formLabelWidth">
              <el-input v-model="formLabelAlign.container" autocomplete="off"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
           <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
          </div>
      </el-dialog>
    <el-button type="info" plain icon="el-icon-delete" @click="open">删除</el-button>
    <el-button type="info" plain icon="el-icon-edit" @click="dialogFormVisible = true">更改</el-button>
      <el-dialog title="编辑" :visible.sync="dialogFormVisible" width="33%">
          <el-form :model="formLabelAlign" label-width="100px" style="width:400px">
            <el-form-item label="地点:" :label-width="formLabelWidth">
              <el-input v-model="formLabelAlign.location" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="教室号:" :label-width="formLabelWidth">
              <el-input v-model="formLabelAlign.number" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="校区:" :label-width="formLabelWidth">
              <el-input v-model="formLabelAlign.area" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="楼栋:" :label-width="formLabelWidth">
              <el-input v-model="formLabelAlign.building" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="教室容量:" :label-width="formLabelWidth">
              <el-input v-model="formLabelAlign.container" autocomplete="off"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
           <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
          </div>
      </el-dialog>
    <el-button type="info" plain icon="el-icon-document">导入考场信息</el-button>

    <el-table
    :data="tableData"
    height="525"
    border
    :header-cell-style="{background:'#eef1f6',color:'#606266'}"
    :row-style="{height:'40px'}"
    style="width: 100%">
    <el-table-column
      align="center"
      prop="tnumber"
      label="考场编号"
      width="">
    </el-table-column>
    <el-table-column
      align="center"
      prop="location"
      label="地点"
      width="">
    </el-table-column>
    <el-table-column
      align="center"
      prop="rnumber"
      label="教室号"
      width="">
    </el-table-column>
      <el-table-column
      align="center"
      prop="area"
      label="校区"
      width="">
      </el-table-column>
      <el-table-column
      align="center"
      prop="building"
      label="楼栋（0-新区，1-老区）"
      width="">
      </el-table-column>
      <el-table-column
      align="center"
      prop="rcontainer"
      label="教室容量"
      width="">
      </el-table-column>
  </el-table>
  </div>
</template>

<script>
export default {
  name: 'rmessage',
  data () {
    return {
      dialogFormVisible: false,
      formLabelAlign: {
          location:'',
          number:'',
          area:'',
          building:'',
          container:'',
        },
        formLabelWidth: '100px',
      tableData: [{
         
      }],
    }
  },
  methods: {
   open() {
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .line2{
    width: 200px;
    height: 10px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
  }
  .btn{
    margin-bottom: 20px;
  }
  .el-table{
  border: solid #eef1f6;
}
  .el-button{
    margin-bottom: 20px;
  }
</style>


