<template>
    <div id="lmessage">
        <div class="line">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item :to="{ path: '/First' }">首页管理</el-breadcrumb-item>
                <el-breadcrumb-item>课程信息</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        课程名称：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        <el-input
            style="width: 250px"
            placeholder="请输入"
            prefix-icon="el-icon-search"
            v-model="input1"
            clearable>
        </el-input>
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        <el-button type="info" plain>查询</el-button>
        <el-divider></el-divider>
        <el-button type="info" plain icon="el-icon-circle-plus-outline" @click="dialogFormVisible = true">增加</el-button>
            <el-dialog title="编辑" :visible.sync="dialogFormVisible" width="33%">
                 <el-form :model="formLabelAlign" label-width="100px" style="width:400px">
                    <el-form-item label="课程名称:" :label-width="formLabelWidth">
                        <el-input v-model="formLabelAlign.lname" autocomplete="off"></el-input>
                    </el-form-item>
                 </el-form>
                 <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
                </div>
            </el-dialog>
        <el-button type="info" plain icon="el-icon-delete" @click="open">删除</el-button>
        <el-button type="info" plain icon="el-icon-edit" @click="dialogFormVisible = true">更改</el-button>
            <el-dialog title="编辑" :visible.sync="dialogFormVisible" width="33%">
                 <el-form :model="formLabelAlign" label-width="100px" style="width:400px">
                    <el-form-item label="课程名称:" :label-width="formLabelWidth">
                        <el-input v-model="formLabelAlign.lname" autocomplete="off"></el-input>
                    </el-form-item>
                 </el-form>
                 <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
                </div>
            </el-dialog>
        <el-button type="info" plain icon="el-icon-document">导入考场信息</el-button>
        <el-table
    :data="tableData"
    height="415"
    border
    :header-cell-style="{background:'#eef1f6',color:'#606266'}"
    :row-style="{height:'40px'}"
    style="width: 100%">
    <el-table-column
      align="center"
      prop="lnum"
      label="课程编号"
      width="">
    </el-table-column>
    <el-table-column
      align="center"
      prop="lname"
      label="课程名称"
      width="">
    </el-table-column>
  </el-table>
    </div>
</template>

<script>
export default {
    name: 'lmessage',
    data() {
        return{
            input: '',
            dialogFormVisible: false,
            formLabelAlign: {
                lnum: '',
                lname: ''
            },
            tableData: [{
         
            }]
        }
    },
     methods: {
             open() {
                this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$message({
                type: 'success',
                 message: '删除成功!'
            });
            }).catch(() => {
                this.$message({
                type: 'info',
                message: '已取消删除'
            });          
        });
      }
     }    
}
</script>

<style scoped>
    .line{
    width: 200px;
    height: 80px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
  }
  .el-table{
  border: 1px solid #eef1f6;
  position: relative;
  top: 20px;
}
</style>


