<template>
    <div id="trmessage">
        <div class="line">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item :to="{ path: '/First' }">首页管理</el-breadcrumb-item>
                <el-breadcrumb-item>教师信息</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        学院名称：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        <el-select v-model="value1" placeholder="请选择" clearable>
            <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
            </el-option>
        </el-select>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        教师姓名：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<el-input v-model="input1" placeholder="请输入内容" style="width:221.4px"></el-input>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        角色名称：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<el-input v-model="input2" placeholder="请输入内容" style="width:221.4px"></el-input>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        <el-button type="info" plain>查询</el-button>
        <el-divider></el-divider>
        <el-button type="info" plain icon="el-icon-circle-plus-outline" @click="dialogFormVisible = true">增加</el-button>
        <el-dialog title="编辑" :visible.sync="dialogFormVisible" width="33%">
          <el-form :model="formLabelAlign" label-width="100px" style="width:400px">
            <el-form-item label="教师姓名:" :label-width="formLabelWidth">
              <el-input v-model="formLabelAlign.tname" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="教师工号:" :label-width="formLabelWidth">
              <el-input v-model="formLabelAlign.tnum" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="学院名称:" :label-width="formLabelWidth">
            <el-select v-model="value2" clearable placeholder="请选择" style="width:300px">
            <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
            </el-form-item>
            <el-form-item label="工资号:" :label-width="formLabelWidth">
              <el-input v-model="formLabelAlign.munm" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="专业名称:" :label-width="formLabelWidth">
              <el-input v-model="formLabelAlign.spname" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="角色名称:" :label-width="formLabelWidth">
              <el-input v-model="formLabelAlign.cha" autocomplete="off"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
           <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
          </div>
      </el-dialog>
    <el-button type="info" plain icon="el-icon-delete" @click="open">删除</el-button>
    <el-button type="info" plain icon="el-icon-edit" @click="dialogFormVisible2 = true">更改</el-button>
      <el-dialog title="编辑" :visible.sync="dialogFormVisible" width="33%">
          <el-form :model="formLabelAlign2" label-width="100px" style="width:400px">
            <el-form-item label="教师姓名:" :label-width="formLabelWidth">
              <el-input v-model="formLabelAlign2.tname2" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="教师工号:" :label-width="formLabelWidth">
              <el-input v-model="formLabelAlign2.tnum2" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="学院名称:" :label-width="formLabelWidth">
            <el-select v-model="value3" clearable placeholder="请选择" style="width:300px">
            <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
            </el-form-item>
            <el-form-item label="工资号:" :label-width="formLabelWidth">
              <el-input v-model="formLabelAlign2.mnum2" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="专业名称:" :label-width="formLabelWidth">
              <el-input v-model="formLabelAlign2.spname2" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="角色名称:" :label-width="formLabelWidth">
              <el-input v-model="formLabelAlign2.cha2" autocomplete="off"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
           <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
          </div>
      </el-dialog>
    <el-button type="info" plain icon="el-icon-document">导入考场信息</el-button>
    <el-table
    :data="tableData"
    height="435"
    border
    :header-cell-style="{background:'#eef1f6',color:'#606266'}"
    :row-style="{height:'40px'}"
    style="width: 100%">
    <el-table-column
      align="center"
      prop="tname"
      label="教师姓名"
      width="">
    </el-table-column>
    <el-table-column
      align="center"
      prop="tnum"
      label="教师编号"
      width="">
    </el-table-column>
    <el-table-column
      align="center"
      prop="cname"
      label="学院名称"
      width="">
    </el-table-column>
      <el-table-column
      align="center"
      prop="mnum"
      label="工资号"
      width="">
      </el-table-column>
      <el-table-column
      align="center"
      prop="spnum"
      label="专业名称"
      width="">
      </el-table-column>
      <el-table-column
      align="center"
      prop="cha"
      label="角色名称"
      width="">
      </el-table-column>
  </el-table>
    </div>
</template>

<script>
export default {
    name: 'trmessage',
    data(){
        return{
            input1: '',
            input2: '',
            options: [{
                value: '选项1',
                label: '教师'
            }],
            value1: '',
            value2: '',
            value3: '',
            dialogFormVisible: false,
            formLabelAlign: {
                tname:'',
                tnum:'',
                cname:'',
                mnum:'',
                spnum:'',
                cha: ''
            },
            formLabelAlign2: {
                tname2:'',
                tnum2:'',
                cname2:'',
                mnum2:'',
                spnum2:'',
                cha2: ''
            },
            formLabelWidth: '100px',
            tableData: [{
         
            }],
            }
      },
      methods: {
   open() {
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      }
  }
}
    
</script>

<style scoped>
.line{
    width: 200px;
    margin-left: auto;
    margin-right: auto;
    height: 60px;
    margin-top: 50px;
  }
 .el-table{
  border: 1px solid #eef1f6;
  position: relative;
  top: 20px;
}
</style>


