<template>
  <div class="cmassage">
    <div class="line">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/First' }">首页管理</el-breadcrumb-item>
        <el-breadcrumb-item>学院信息</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="demo-input-suffix">
    学院名称：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
    <el-input
      style="width: 250px"
      placeholder="请输入"
      prefix-icon="el-icon-search"
      v-model="input"
      clearable>
    </el-input>
    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
    <el-button type="info" plain>查询</el-button>
    <el-divider></el-divider>
    <div class="btn2">
      <el-button type="info" plain icon="el-icon-circle-plus-outline" @click="dialogFormVisible = true">增加</el-button>
        <el-dialog title="编辑" :visible.sync="dialogFormVisible">
          <el-form :model="form" label-width="80px">
            <el-form-item label="学院名称">
            <el-select v-model="form.region" placeholder="请选择活动区域">
              <el-option label="区域一" value="shanghai"></el-option>
              <el-option label="区域二" value="beijing"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="学院编号">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        </el-form>
    <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible = false">取 消</el-button>
    <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
    </div>
    </el-dialog>
      <el-button type="info" plain icon="el-icon-delete" @click="open2">删除</el-button>
      <el-button type="info" plain icon="el-icon-edit" @click="dialogFormVisible = true">更改</el-button>
        <el-dialog title="编辑" :visible.sync="dialogFormVisible">
          <el-form :model="form" label-width="80px">
            <el-form-item label="学院名称">
            <el-select v-model="form.region" placeholder="请选择活动区域">
              <el-option label="区域一" value="shanghai"></el-option>
              <el-option label="区域二" value="beijing"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="学院编号">
          <el-input v-model="form.name" autocomplete="off" style="width: 220px"></el-input>
        </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
        </div>
      </el-dialog>
    </div>
    <el-table
    :data="tableData"
    height="415"
    border
    :header-cell-style="{background:'#eef1f6',color:'#606266'}"
    :row-style="{height:'40px'}"
    style="width: 100%">
    <el-table-column
      align="center"
      prop="cname"
      label="学院名称"
      width="">
    </el-table-column>
    <el-table-column
      align="center"
      prop="number"
      label="学院编号"
      width="">
    </el-table-column>
  </el-table>
  </div>
</div>
</template>

<script>
export default {
  name: 'cmessage',  
  data () {
    return {
      input: '',
      dialogVisible: false,
      dialogFormVisible: false,
        form: {
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        },
      tableData: [{
         
      }]
    }
  },
  methods: {
      /*open() {
        this.$prompt('请输入', '编辑', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          //inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
          inputErrorMessage: ''
        }).then(({ value }) => {
          this.$message({
            type: 'success',
            message: '已添加: ' + value
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          });       
        });
      },*/
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
    },
       open2() {
        this.$confirm('此操作将删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      }
}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .line{
    width: 200px;
    height: 10px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
}
  .btn2{
    margin-bottom: 20px;
}
  .demo-input-suffix{
    height: 20px;
    margin-top: 70px;
  }
  .el-table{
  border: 1px solid #eef1f6;
}
</style>


