<template>
  <div class="first">
    <div class="image" style="font-size: 60px">
      湖北文理学院
      <el-image :src="imgUrl"></el-image>
      考务管理系统
    </div>
  </div>
</template>

<script>
import 学校 from '@/assets/学校.jpg'
export default {
  name: 'First',
  data () {
    return{
      imgUrl: 学校
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-image{
    width: 300px;
    height: 300px;
    margin-top: 100px;
  }
</style>


