import Vue from 'vue'
import Router from 'vue-router'
//import HelloWorld from '@/components/HelloWorld'
import Cmessage from '@/components/Cmessage'
import Rmessage from '@/components/Rmessage'
import First from '@/components/First'
import Trmessage from '@/components/Trmessage'
import Lemessage from '@/components/Lemessage'
import Textmessage from '@/components/Textmessage'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    /*{
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },*/
    {
      path: '/First',
      name: 'First',
      component: First
    },
    {
      path: '/Cmessage',
      name: 'Cmessage',
      component: Cmessage
    },
    {
      path: '/Rmessage',
      name: 'Rmessage',
      component: Rmessage
    },
    {
      path: '/Trmessage',
      name: 'Trmessage',
      component: Trmessage
    },
    {
      path: '/Lemessage',
      name: 'Lemessage',
      component: Lemessage
    },
    {
      path: '/Textmessage',
      name: 'Textmessage',
      component: Textmessage
    }
  ]
})



